<template>
  <div class="dayQualityControl tapStyle secondary-page-mode">
    <div class="currency-mode-wrap" style="margin-top: 0;height: 100%;">
      <div class="reminder-wrap">
        <span class="reminder-title">温馨提示</span>
        <div class="reminder-content" style="width: 100%">
          <div class="reminder-text">
            <p>该点位最新一次质控参数设置于{{controlTips.LATESTTIME}}，至今已使用{{controlTips.TODATE}}；期间日质控合格了{{controlTips.QUALIFIED}}次；
              不合格了{{controlTips.UNQUALIFIED}}次。</p>  <!-- 造成无效数据xx条 -->
            <div><el-button type="noticonsecondBtn"> 生成任务 </el-button></div>
          </div>
        </div>
      </div>
      <div class="currency-body-wrap" style="height: calc(100% - 52px);">
        <el-scrollbar style="height: 100%;">
          <div class="modular-box">
            <div class="modular-header">
              <span class="title">当前使用周质控参数信息</span>
            </div>
            <div class="modular-body">
              <div class="modular-row clear">
                <div class="statistics-text clear">
                  <span>目标水质：{{siteInfo.targerWaterQuality}}</span>
                  <span>当前水质：{{siteInfo.currentWaterQuality}}</span>
                </div>
                <div class="statistics-text clear">
                  <span>距上次质控已有{{controlTips.DATATODATE}}</span>
                </div>
                <div class="toolbar">
                  <el-button @click="dailyQuality=true" style="margin: 0;display: inline-block;vertical-align: middle;">参数设置</el-button>
                </div>
              </div>
              <div class="table-box" style="padding: 0 6px 6px;">
                <el-table
                  :data="tableData"
                  border
                  style="width: 100%">
                  <el-table-column
                    prop="indexName"
                    label="监测项目">
                  </el-table-column>
                  <el-table-column
                    prop="standardValue"
                    label="标准溶液浓度">
                    <template slot-scope="scope">
                      <span>{{scope.row.standardValue}} ({{scope.row.indexUnit}})</span>
                    </template>
                  </el-table-column>
                 <!-- <el-table-column
                    prop="solution"
                    width="160"
                    label="标准溶液浓度">
                  </el-table-column>-->
                  <el-table-column
                    prop="submitTime"
                    label="提交时间">
                  </el-table-column>
                  <el-table-column
                    prop="submitter"
                    label="提交人">
                  </el-table-column>
                  <!-- <el-table-column
                    prop="auditTime"
                    width="160"
                    label="审核时间">
                  </el-table-column>
                  <el-table-column
                    prop="auditor"
                    :show-overflow-tooltip="true"
                    label="审核人">
                  </el-table-column> -->
                  <!--<el-table-column
                    prop="operation"
                    width="100px"
                    label="操作">
                    <template slot-scope="scope">
                      <span v-if="scope.row.operation === 0" style="color: #666">无相应权限</span>
                      <el-button @click="singleFactor = true" type="secondBtn">设置</el-button>
                    </template>
                  </el-table-column>-->
                  <!-- <el-table-column
                    prop="historicalRecord"
                    width="100px"
                    label="审核">
                    <template slot-scope="scope">
                      <span v-if="scope.row.operation === 0" style="color: #666">无相应权限</span>
                      <el-button type="secondBtn">审核</el-button>
                    </template>
                  </el-table-column> -->
                </el-table>
              </div>
            </div>
          </div>
          <div class="modular-box">
            <div class="modular-header">
              <span class="title">历史周质控参数信息</span>
              <div class="toolbar">
                <span>时间范围 : </span>
                <div class="layoutBox" style="width:242px">
                  <el-date-picker v-model="dateRange" align="right" type="daterange" start-placeholder="开始日期" end-placeholder="结束日期"></el-date-picker>
                </div>&emsp;
                <el-button style="margin: 0;" @click="historyWeekQuality()"><i class="iconfont icon-sousuo"></i>查询</el-button>
              </div>
            </div>
            <div class="modular-body">
              <div class="modular-row">
                <div class="state-radio-wrap" style="padding-bottom: 6px;">
                  <el-radio-group v-model="stateRadio" size="small" @change="historyWeekQuality">
                    <el-radio-button label="w01010">水温</el-radio-button>
                    <el-radio-button label="w01009">溶解氧</el-radio-button>
                    <el-radio-button label="w01001">PH</el-radio-button>
                    <el-radio-button label="w01014">电导率</el-radio-button>
                    <el-radio-button label="w01003">浊度</el-radio-button>
                  </el-radio-group>
                </div>
                <!-- <div class="statistics-text clear">
                  <span class="title">水温（℃）</span>
                </div> -->
                <div class="statistics-text clear">
                  <span>该时间段共有周质控{{ qualityControlInfo.count }}次，
                    合格{{qualityControlInfo.qualified}}次，不合格{{ qualityControlInfo.unqualified }}次，影响数据y条</span>
                </div>
              </div>
              <div class="table-box" style="padding: 0 6px 6px;">
                <el-table
                  :data="tableDataB"
                  border
                  style="width: 100%">
                  <el-table-column
                    prop="monthWeek"
                    label="周数">
                  </el-table-column>
                  <el-table-column
                    prop="submitTime"
                    label="提交时间">
                  </el-table-column>
                  <el-table-column
                    prop="submitter"
                    label="提交人">
                  </el-table-column>
                  <!-- <el-table-column
                    prop="auditTime"
                    label="审核时间">
                  </el-table-column>
                  <el-table-column
                    prop="auditor"
                    label="审核人">
                  </el-table-column> -->
                  <el-table-column
                    prop="zeroStandard"
                    label="仪器测试值">
                  </el-table-column>
                  <el-table-column
                    prop="standardValue"
                    label="标准溶液浓度">
                  </el-table-column>
                  <el-table-column
                    prop="error"
                    label="误差">
                  </el-table-column>
                  <el-table-column
                    prop="impactData"
                    label="影响数据">
                  </el-table-column>
                </el-table>
              </div>
            </div>
          </div>
        </el-scrollbar>
      </div>
    </div>

    <!-- 参数设置dialog弹窗 -->
    <el-dialog
      width="480px" title="项目设置详情"
      @close="resetProjectListData()"
      :visible.sync="dailyQuality" >
      <div class="daily-quality-box" style="padding: 8px;">
        <div class="daily-head-wrap">
          <div class="row clear">
            <span class="label">当前点位：{{siteInfo.nodeName}}</span>
          </div>
          <div class="row clear">
            <span class="label">目标水质：{{siteInfo.targerWaterQuality}}</span>
            <span class="label">当前水质：{{siteInfo.currentWaterQuality}}</span>
          </div>
          <div class="row clear">
            <span class="label">所属区域：{{siteInfo.areaName}}</span>
            <span class="label">所属流域：{{siteInfo.basinName}}</span>
          </div>
        </div>
        <div class="daily-table-wrap">
          <table class="normal-table" style="width: 100%">
            <tr>
              <th width="134px;">监测项目</th>
              <th>标准溶液浓度</th>
            </tr>
            <tr v-for="(item,index) in projectList" :key="index">
              <td>{{ item.name }}</td>
              <td> <div class="layoutBox" style="width:100px"><el-input v-model="item.value"></el-input></div> <span class="company-text">（{{ item.company }}）</span></td>
            </tr>
            <!--<tr>
              <td>pH</td>
              <td> <div class="layoutBox" style="width:100px"><el-input v-model="tableInput"></el-input></div>（mg/L）</td>
            </tr>
            <tr>
              <td>溶解氧</td>
              <td> <div class="layoutBox" style="width:100px"><el-input v-model="tableInput"></el-input></div>（mg/L）</td>
            </tr>
            <tr>
              <td>电导率</td>
              <td> <div class="layoutBox" style="width:100px"><el-input v-model="tableInput"></el-input></div>（mg/L）</td>
            </tr>
            <tr>
              <td>浊度</td>
              <td> <div class="layoutBox" style="width:100px"><el-input v-model="tableInput"></el-input></div>（mg/L）</td>
            </tr>-->
          </table>
          <div class="btn-wrap">
            <el-button @click="projectSettingsQuality()" style="width: 100px;">提交</el-button>
          </div>
        </div>
      </div>
    </el-dialog>

    <!-- 参数设置dialog弹窗 -->
    <el-dialog
      width="550px" title="单因子质控设置"
      :visible.sync="singleFactor" >
      <div class="daily-quality-box" style="padding: 8px;">
        <div class="daily-head-wrap">
          <div class="row clear">
            <span class="label">当前点位：XXXXXX</span>
          </div>
          <div class="row clear">
            <span class="label">目标水质：XXXXXX</span>
            <span class="label">当前水质：XXXXXX</span>
          </div>
          <div class="row clear">
            <span class="label">所属区域：XXXXXX</span>
            <span class="label">所属流域：XXXXXX</span>
          </div>
        </div>
        <div class="daily-table-wrap">
          <table class="normal-table" width="100%">
            <tr>
              <th width="134px;">监测项目</th>
              <th>高锰酸盐</th>
            </tr>
            <tr>
              <td>上一周的水质平均值</td>
              <td>1.9（mg/L）</td>
            </tr>
            <tr>
              <td>跨度值</td>
              <td> <div class="layoutBox" style="width:140px"><el-input v-model="tableInput"></el-input></div>（mg/L）</td>
            </tr>
            <tr>
              <td>零点标准溶液浓度</td>
              <td> <div class="layoutBox" style="width:140px"><el-input v-model="tableInput"></el-input></div>（mg/L）</td>
            </tr>
            <tr>
              <td>跨度标准溶液浓度</td>
              <td> <div class="layoutBox" style="width:140px"><el-input v-model="tableInput"></el-input></div>（mg/L）</td>
            </tr>
            <tr>
              <td></td>
              <td><el-button>提交</el-button></td>
            </tr>
          </table>
          <div class="btn-wrap">
            <el-button style="width: 100px;">批量提交</el-button>
          </div>
        </div>
      </div>
    </el-dialog>

  </div>
</template>

<script>
  export default {
    name: 'dayQualityControl',
    data(){
      return{
        value:[],
        dateRange:'',
        stateRadio:'w01010',
        stationId:'',
        optionData: [
          { value: 1, label: "水温", standard: "", isShow: true },
          { value: 2, label: "PH", standard: "6~9", isShow: true },
          { value: 3, label: "溶解氧", standard: "≥5", isShow: true },
          { value: 4, label: "电导率", standard: "", isShow: true },
          { value: 5, label: "浊度", standard: "", isShow: true },
          { value: 6, label: "高锰酸盐", standard: "6", isShow: true },
          { value: 7, label: "氨氮", standard: "1", isShow: true },
          { value: 8, label: "总磷", standard: "0.2", isShow: true },
          { value: 9, label: "总氮", standard: "", isShow: true }
        ],
        tableData:[],
        tableDataB:[],
        dailyQuality: false,
        tableInput:'',
        singleFactor: false,
        dialogData:[
          {
            monitoringItems:'高锰酸钾',
            waterQuality:'',
            spanValue:'',
            zeroPoint:'',
            span:'',
          },{
            monitoringItems:'氨氮',
            waterQuality:'',
            spanValue:'',
            zeroPoint:'',
            span:'',
          },{
            monitoringItems:'总磷',
            waterQuality:'',
            spanValue:'',
            zeroPoint:'',
            span:'',
          },{
            monitoringItems:'总氮',
            waterQuality:'',
            spanValue:'',
            zeroPoint:'',
            span:'',
          }
        ],
        projectList:[
          {
            name:'水温',
            code:'w01010',
            value:'',
            company:'mg/L'
          },{
            name:'溶解氧',
            code:'w01009',
            value:'',
            company:'μS/cm'
          },{
            name:'PH',
            code:'w01001',
            value:'',
            company:'无量纲'
          },{
            name:'电导率',
            code:'w01014',
            value:'',
            company:'NTU'
          },{
            name:'浊度',
            code:'w01003',
            value:'',
            company:'℃'
          }
        ],
        resetProjectList:[],
        //站点信息
        siteInfo:{
          nodeName:'',//当前站点
          targerWaterQuality:'',//目标水质
          areaName:'',//所属区域
          basinName:'',//所属流域
          currentWaterQuality:''//当前水质
        },
        qualityControlInfo:{
          count: '0',
          qualified: '0',
          unqualified: '0'
        },
        //温馨提示信息
        controlTips:{
          LATESTTIME:'',
          TODATE:'',
          QUALIFIED:'',
          UNQUALIFIED:'',
          DATATODATE:''
        }
      }
    },
    methods:{
      //获取周质控当前使用24小时质控参数信息
      getCurrentData(){
        this.$http.get("/Quality/qualityControlSet/currentWeekQualityControlParam", {
          params: {
            stationCodes:  this.stationId
          }
        }).then(res=>{
          if(res.data.code === 0){
            this.tableData = res.data.content.dataList;
          }
        })
      },
      //获取周质控历史质控参数信息
      historyWeekQuality(){
        this.$http.get("/Quality/qualityControlSet/historyWeekQualityControlParam", {
          params: {
            startTime: this.dateRange[0].format('yyyy-MM-dd'),
            endTime: this.dateRange[1].format('yyyy-MM-dd'),
            indexCodes: this.stateRadio,
            stationCodes:  this.stationId
          }
        }).then(res=>{
          if(res.data.code === 0){
            this.tableDataB = res.data.content.dataList;
            this.getWeeklyQualityControl();
            this.getWeeklyQualityControlTips();
          }
        })
      },
      //获取周质控历史质控参数信息
      projectSettingsQuality(){
        let list = [];
        this.projectList.forEach((item)=>{
          let obj = {
            stationCodes:'10005',
            indexCode:item.code,
            standardValue:item.value,
          };
          list.push(obj);
        });
        this.$http.post("/Quality/qualityControlSet/editorCurrentWeekQualityControlParam", list).then(res=>{
          if(res.data.code === 0){
            this.$message({
              message: res.data.message,
              type: 'success'
            });
            this.getCurrentData();
            this.historyWeekQuality();
            this.resetProjectListData();
            this.dailyQuality = false;
          }
        });
      },
      //重置项目设置详情弹窗表格的数据
      resetProjectListData(){
        this.projectList = JSON.parse(JSON.stringify(this.resetProjectList));
      },
      //转换水质文字
      conversionWaterQuality(tag){
          let num = parseInt(tag);
          let str = '';
          switch(num){
            case 1:
              str =  'Ⅰ类';
            case 2:
              str =  'Ⅱ类';
            case 3:
              str =  'Ⅲ类';
            case 4:
              str =  'Ⅳ类';
            case 5:
              str =  'Ⅴ类';
          }
          return str;
      },
      //获取当前水质
      getCurrentWaterQuality(){
        this.$http.get("/Quality/qualityControlSet/getStationCurrentWaterQualityData", {
          params: {
            stationCode:  this.stationId
          }
        }).then(res=>{
           if(res.data.code === 0){
             this.siteInfo.currentWaterQuality = this.conversionWaterQuality(res.data.content.info);
           }
        })
      },
      //获取周质控次数
      getWeeklyQualityControl(){
        this.$http.get("/Quality/qualityControlSet/qualityControlConclusion", {
          params: {
            startTime: this.dateRange[0].format('yyyy-MM-dd'),
            endTime: this.dateRange[1].format('yyyy-MM-dd'),
            indexCodes: this.stateRadio,
            stationCodes:  this.stationId,
            qualityAnalysisType: 'WEEK_QUALITY',
            ifCurrentSection: true
          }
        }).then(res=>{
           if(res.data.code === 0){
             if(res.data.content.info.count){
               this.qualityControlInfo = res.data.content.info;
             }else{
               this.qualityControlInfo.count = 0;
               this.qualityControlInfo.qualified = 0;
               this.qualityControlInfo.unqualified = 0;
             }
           }
        })
      },
      getWeeklyQualityControlTips(){
        this.$http.get("/Quality/qualityControlSet/qualityControlTips", {
          params: {
            stationCodes:  this.stationId,
            qualityAnalysisType: 'WEEK_QUALITY',
          }
        }).then(res=>{
           if(res.data.code === 0){
             console.log('2222',res);
             this.controlTips = res.data.content.info;
           }
        })
      }
    },
    created(){
      this.$store.commit('setShowlefttree',true);
      this.dateRange = [new Date(new Date().setDate(new Date().getDate()-10)),new Date()];

      this.resetProjectList = JSON.parse(JSON.stringify(this.projectList));

      //测试使用=》开始
      // this.stationId = '10005';
      // this.$nextTick(()=>{
      //   this.getCurrentData();
      //   this.historyWeekQuality();
      // });
      //测试使用=》结束
    },
    computed: {
      totalPageNumber() {
        //计算总页数
        let number = 0;
        number = Math.ceil(this.total / this.pageSize);
        return number;
      },
      stationInfo: {
        get: function() {
          return this.$store.state.stationInfo;
        }
      }
    },
    watch: {
      stationInfo(value) {
        if (value.length > 0) {
          this.stationId = value[0].nodeId;

          this.siteInfo.nodeName = value[0].nodeName;//当前站点赋值
          this.siteInfo.targerWaterQuality = this.conversionWaterQuality(value[0].targerWaterQuality);//目标水质赋值
          this.siteInfo.areaName = value[0].areaName;//所属区域赋值
          this.siteInfo.basinName = value[0].basinName;//所属流域赋值

          this.getCurrentData();
          this.historyWeekQuality();
          this.getCurrentWaterQuality();
          // this.getWeeklyQualityControl();
        }
      }
    }
  }
</script>

<style scoped lang="less">
  .editInput{
    width: 50px;
    border: none;
    outline: none;
  }
  .modular-box{
    border: 1px solid #dce9fa;
    margin-top: 6px;
    .modular-header{
      height: 40px;
      line-height: 40px;
      padding: 0 8px;
      border-bottom: 1px solid #dce9fa;
      .title{
        font-size: 16px;
      }
      .title:before{
        content:'';
        display: inline-block;
        vertical-align: middle;
        width: 3px;
        height: 10px;
        background: #166bce;
        margin-bottom: 3px;
        margin-right: 16px;
      }
    }
    .modular-body{
      min-height: 100px;
    }
    .toolbar{
      float: right;
      line-height: 38px;
      &>button{
        margin-bottom: 3px;
      }
    }
    .modular-row{
      padding: 6px;
    }
  }
  .currency-body-wrap{
    height: calc(100%);
  }
  .statistics-text{
    display: inline-block;
    vertical-align: top;
    position: relative;
    background: #e6f1fc;
    border: 1px solid #cad9eb;
    line-height: 36px;
    height: 36px;
    padding: 0 10px;
    .title{
      color: #186cce;
      font-size: 16px;
    }
    &>span{
      margin-right: 24px;
    }
    &>span:last-child{
      margin-right: 0;
    }
    &:before{
      content:'';
      position: absolute;
      width: 7px;
      height: 7px;
      border: 2px solid #166bce;
      border-right: none;
      border-bottom: none;
      top: -1px;
      left: -1px;
    }
    &:after{
      content:'';
      position: absolute;
      width: 7px;
      height: 7px;
      border: 2px solid #166bce;
      border-top: none;
      border-left: none;
      right: -1px;
      bottom: -1px;
    }
  }
  /* 日质控设置弹窗样式 */
  .daily-quality-box{
    .daily-head-wrap{
      .row{
        padding-bottom: 10px;
        &>span{
          width: 50%;
          float: left;
        }
      }
    }
    .daily-table-wrap{
      .normal-table{
        th{
          text-align: center;
          height: 40px;
        }
        td{
          text-align: center;
          padding: 4px;
          height: 40px;
        }
      }
      .btn-wrap{
        text-align: center;
        padding-top: 10px;
      }
    }
  }
  .company-text{
    display: inline-block;
    vertical-align: top;
    width: 68px;
    text-align: left;
  }
</style>



